<template>
	<view>
		<view class="uni-padding-wrap uni-common-mt">
						<uni-segmented-control :current="current" :values="items" :style-type="styleType"
							:active-color="activeColor" @clickItem="onClickItem" />
		</view>
		<view  class="news_item"   v-if="current===0"  v-for="(item,index) in newsList" :key="item.id">
			<!-- <view v-if="current===0" > -->
				<uni-card :title="item.content"  :extra="item.address"  padding="10px 0" :thumbnail="avatar" isShadow="true"  shadow="6px 6px 20px 6px rgba(0, 0, 0, 0.19)">
					<view hover-stop-propagation="true">
						<image style="width: 100%;"   @click="goDetail(index)" src="https://img-prod-cms-rt-microsoft-com.akamaized.net/cms/api/am/imageFileData/RE4wtd6?ver=364f"></image>
					</view>
								
								<text class="uni-body uni-mt-5">维修详情</text>
								
								<uni-row>
									<uni-col :span="16" pull="1">
										<uni-list>
											<uni-list-item title="维修人:" :rightText=getDidName(item.did) clickable="true" @click="pickPerson(index)" ></uni-list-item>
										</uni-list>
									</uni-col>
									<uni-col :span="8" push="0">
										<button class="button popup-success" type="primary" @click="submit(index)">分配订单</button>
									</uni-col>
								</uni-row>
							</uni-card>
				
			
		</view>
		
		
		<view     v-if="current===1"  v-for="(item,index) in finished_list" :key="item.id">
			<uni-card :title="item.content"  @click="finished_goDetail(index)" isShadow="true" shadow="6px 6px 20px 6px rgba(0, 0, 0, 0.19)"  thumbnail="https://img-prod-cms-rt-microsoft-com.akamaized.net/cms/api/am/imageFileData/RE4wtd6?ver=364f"  >
				<text >{{item.address}}</text>
			</uni-card>
		</view>
		
		
		<view v-if="current===1">
			<uni-fab ref="fab" :pattern="pattern" :content="content" :horizontal="horizontal" :vertical="vertical"
						:direction="direction" @trigger="trigger" @fabClick="fabClick(e)" />
		</view>
		
		<view>
			<uni-drawer ref="showLeft" mode="left" :width="250" @change="change($event,'showLeft')">
				<view class="close">
					<view v-for="(item,index) in Person" @click="choose(index)">
						<uni-list>
							<uni-list-item :title=item.departmentname
							 ></uni-list-item>
						</uni-list>
					</view>
					
					<button @click="closeDrawer('showLeft')" type="primary"><text >取消选择</text></button>
				</view>
			</uni-drawer>
		</view>
		<view>
					<!-- 提示窗示例 -->
				<uni-popup ref="alertDialog" type="dialog">
					<uni-popup-dialog :type="msgType" confirmText="同意" cancelText="关闭"  title="警告" content="确认分配此订单？" @confirm="dialogConfirm()"
						@close="dialogClose()">
					</uni-popup-dialog>
				</uni-popup>
		</view>
	</view>
</template>

<script>
	
	export default {
		components: {},
		data() {
			return {
				repaire:"",
				repaire_index:-1,
				button_index:-1, //提交按钮索引
				first_picPath:[],
				newsList:[{
					content:"水管坏了",
					address:"荔园食堂背后",
					id:2
				}],
				
				finished_list:[],
				
				Person:["张三","李四","王五"],
				index:0, //代表选中【全部订单，已受理，派工，完工】 中的索引值，根据索引值进行渲染
				items: ['未分配订单', '已分配订单'],
				options2: [{
										text: '取消',
										style: {
											backgroundColor: '#007aff'
										}
									},
									{
										text: '确认',
										style: {
											backgroundColor: '#F56C6C'
										}
									}
						],
				styles: [{
						value: 'button',
						text: '按钮',
						checked: true
					},
					{
						value: 'text',
						text: '文字'
					}
				],
				current: 0,
				activeColor: '#007aff',
				styleType: 'button',
				newsList:[{
					content:"维修问题",
					address:"维修地点"
				},
				{
					content:"维修问题",
					address:"维修地点"
				}
				],
				
				directionStr: '垂直',
				horizontal: 'left',
				vertical: 'bottom',
				direction: 'horizontal',
				pattern: {
					color: '#7A7E83',
					backgroundColor: '#fff',
					selectedColor: '#007AFF',
					buttonColor: '#007AFF',
					iconColor: '#fff'
				},
				is_color_type: false,
				content: [{
						iconPath: '/static/order/file.png',
						selectedIconPath: '/static/order/3.png',
						text: '全部订单',
						active: true
					},
				{
					iconPath: '/static/order/11111.png',
					selectedIconPath:'/static/order/shouli.png',
					text:'已受理',
					active:false
				},
				{
					iconPath: '/static/order/shigongrenyuan.png',
					selectedIconPath: '/static/order/4.png',
					text: '派工',
					active: false
				},
				{
					iconPath: '/static/order/complete.png',
					selectedIconPath: '/static/order/2.png',
					text: '完工',
					active: false
				}
				]
			}
		},
		methods: {
			onClickItem(e) {
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex
				}
			},
			goDetail(index){
				console.log("点击了组件")
				uni.navigateTo({
					url:"./historyDetail",
					events: {
					    // 为指定事件添加一个监听器，获取被打开页面传送到当前页面的数据
					    acceptDataFromOpenedPage: function(data) {
					      console.log("收到子页面传来的信息",data)
					    },
					    confirm_repair: (data) => {
					      console.log("子页面选择维修人：",data)
						  // this.newsList[index].did=2
						  this.newsList[index].did=this.Person[data.repair_arr_index].did
						  console.log(this.newsList[index].did)
						  
					    }
					    
					  },
					success: (res) => {
						console.log(".............",this.Person[1],this.newsList[index].did-1)
						
						res.eventChannel.emit('acceptDataFromOpenerPage', { 
							data: this.Person,
							did:this.newsList[index].did ,
							person: this.newsList[index].did-1>=0? this.Person[this.newsList[index].did-1].departmentname:"",
							question:this.newsList[index].content,
							location:this.newsList[index].address,
							time:this.newsList[index].appointment_time,
							repair_clickable: true,
							latitude:this.newsList[index].latitude,
							longitude:this.newsList[index].longitude,
							rid:this.newsList[index].rid,
							first_path: this.newsList[index].imgs,
							})
					}
				})
			},
			
			finished_goDetail(index){
				uni.navigateTo({
					url:"./historyDetail",
					
					success: (res) => {
						res.eventChannel.emit('acceptDataFromOpenerPage', { 
							data: this.Person,
							did:this.finished_list[index].did ,
							question:this.finished_list[index].content,
							location:this.finished_list[index].address,
							time:this.finished_list[index].appointment_time,
							repair_clickable:false,
							latitude:this.finished_list[index].latitude,
							longitude:this.finished_list[index].latitude,
							// latitude:39.909,
							// longitude:116.39742,
							rid:this.finished_list[index].rid,
							})
					}
				})
			},
			trigger(e) {
							console.log(e)
							this.content[0].active=false;
							this.content[1].active=false;
							this.content[2].active=false;
							this.content[3].active=false;
							this.content[e.index].active = !e.item.active
							this.index=e.index
							console.log(this.index)
						},
						
			pickPerson(index){   //在订单详情页外面点击维修人 显示左侧抽屉
				console.log("dianjiale");
				this.$refs['showLeft'].open();
				console.log(index)
				this.repaire_index=index
			},
			
			closeDrawer(e){
				this.$refs[e].close();
			},
			//选择员工或部门
			choose(index){
				console.log("选择了人员，序号为：",index);
				console.log(this.newsList[this.repaire_index])
				//this.newsList[this.repaire_index].repairer 中的repairer只是用来存储 部门/维修人 编号，不一定是维修人编号也可能是部门编号
				
				// this.newsList[this.repaire_index].repairer=this.Person[index].did;
				this.newsList[this.repaire_index].did=this.Person[index].did;
				this.newsList[this.repaire_index].repairername=this.Person[index].departmentname;
				this.$refs['showLeft'].close();
				
			},
			
			submit(index){
				console.log("订单index: ",index);
				this.button_index=index;
				this.$refs.alertDialog.open()
			},
			
			getDidName(did){
				if (did>0){
					return this.Person[did-1].departmentname;
				}
				return ""
				
			},
			
			change(e,s){
				console.log(e);
			},
			
			dialogClose(){
				console.log("关闭弹窗")
			},
			//加载订单信息
			getInfo(){
				console.log("in getinfo ......");
				this.$util.request("/repair/check_distribution",
				{
					uid : getApp().globalData.id
				},
					(res)=>{
						console.log("get data success")
						console.log(res.data)
						console.log("print this.data.data end")
						this.newsList=res.data.data
						
						
					}
				)
				
				this.$util.request("/repair/check_distributed",{},
					(res) => {
						console.log(res.data.data)
						this.finished_list=res.data.data
						console.log(this.finished_list,typeof(this.finished_list))
					}
				)
				
				
			},
			
			//获取部门维修工或者部门
			getRepair(){
				
				this.$util.request("/user/find_department",{},
					(res) => {
						console.log("in getrepair function")
						console.log("获取维修部门信息返回结果",res)
						this.Person=res.data.data;
						console.log("维修部门信息",this.Person)
					}
				
				)
				
				
			
			},
			
			//确认分配订单之后的操作
			dialogConfirm(){
				console.log("************",this.newsList[this.button_index].rid)
				this.$util.request("/repair/distribute1?rid="+ this.newsList[this.button_index].rid + "&did=" + this.newsList[this.button_index].did,
				{
					
				},
					
				(res)=>{
					console.log(res)
					console.log('分配成功')
					this.getInfo()
				},
				false
				)
				
			}
		},
		onLoad(){
			// this.$refs['showLeft'].close()
			console.log("in onload....")
			this.getInfo();
			this.getRepair();
			
		}
	}
</script>

<style lang="scss">
	@import "@/static/iconfont.css";
	.uni-common-mt {
			margin-top: 30px;
		}
		
	.uni-padding-wrap {
		padding: 0px 30px;
	}
</style>